import router from '@/router/index'
import {RxUtil} from "@/assets/util.js";

export default{
    data(){
        return {
        	data:{
        		password:""
        	}
        }
    },
   
    methods:{
   		
   		editPassword(){
   			if(this.data.password.trim()!=this.data.comfirmPwd.trim()){
				this.errorToast("前后密码不一致");
           		return;
   			}
			var url = _baseUrl +"/mobile/oa/personalInfo/editPassword.do";
			this.$ajax({
				url:url,
				method:'post',
				params:{
					oldPwd:this.data.oldPwd,
					password:this.data.password
				}				
			}
			).then(response => {
				var rtn=response.data;
				if(rtn.success){
					this.successToast(rtn.message);
					router.push({name:'login',params:{source:'editPwd'}});
				}else{
					this.errorToast(rtn.message);
				}
				
			})
			.catch(error=> {
			   console.log(error);
			});	
   		},
    }
}




